@media (min-width: 1025px) {
  .pricing-item:hover {
    &::before {
      width: 100%;      
      transition-delay: .3s;
    }
  }
  .btn-slider-arrow:hover {
    background-color: $secondary;
    span {
      color: $white;
    }
    svg {
      path {
        stroke: $white;
      }
    }
  }
}

@media screen and (max-width: 1560px){
  .results-section {
    background-position: center right;
    .results-text {
      p {
        font-size: 35px;
        line-height: 50px;
      }
    }
  }
}

@media screen and (max-width: 1440px) {
  .section-main {
    background-size: cover;
    background-position: center center;
    &-content {
      padding-top: 3%;
    }
    .services-main-content-advantages {
      margin-top: 0;
      margin-bottom: 30px;
      ul {
        li + li {
          margin-top: 25px;
        }
        li {
          div {
            min-width: 40px;
            width: 40px;
            margin-right: 20px;
          }
          p {
            font-size: 18px;
            line-height: 24px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .results-section {
    padding-top: 100px;
    padding-bottom: 100px;
    .results-block {
      flex-direction: column;
      align-items: flex-start;
    }
    .results-text {
      margin-bottom: 35px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .section-main {
    h1 {
      font-size: 44px;
      line-height: 65px;
      max-width: 100%;
    }
    &-content {
      width: 100%;
      padding-top: 30px;
    }
  }
}

@media screen and (max-width: 768px) {
  .results-section {
    padding-top: 75px;
    padding-bottom: 75px;
    .results-text {
      max-width: 625px;
      margin-bottom: 20px;
      p {
        font-size: 29px;
        line-height: 43px;
      }
    }
  }
}

@media screen and (max-width: 575px) {
  .section-main {
    h1 {
      font-size: 32px;
      line-height: 44px;
      max-width: 100%;
    }
    &-content {
      padding-top: 20px;
    }
  }
  .results-section {
    background-image: url('../img/services-page/results-bg-mobile.jpg');
    background-position: bottom center;
    .results-text {
      p {
        font-size: 32px;
        line-height: 44px;
      }
    }
  }
}

