@import '../variables';

// templates
span.label-step.active {
  background-color: $primary;
}
span.label-step {
  display: block;
  width: fit-content;
  border-radius: 90px;
  background-color: $secondary;
  padding: 10px 20px;
  font-size: 18px;
  line-height: 24px;
  color: $white;
  transition: $cubic;
}
span.hits {
  font-size: 18px;
  line-height: 24px;
  color: $white;
  padding: 10px 20px;
  display: block;
  width: fit-content;
  border-radius: 90px;
  background-color: #FC5045;
  font-weight: 400;
  white-space: nowrap;
}
.stars-wrap {
  display: flex;
  span.star {
    display: inline-block;
    width: 28px;
    height: 27px;
    background-image: url("data:image/svg+xml;base64,");
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
  }
  span.star + span.star {
    margin-left: 22px;
  }
}

// templates



h2 {
  font-size: 44px;
  line-height: 64px;
}
h3 {
  font-weight: 600;
  font-size: 36px;
  line-height: 44px;
}
.btn-slider-arrow {
  display: flex;
  align-items: center;
  position: static;
  right: auto;
  left: auto;
  top: auto;
  width: fit-content;
  height: auto;
  margin: 0;
  white-space: nowrap;
  border-radius: 90px;
  border: 1px solid $secondary;
  padding: 10px 20px;
  outline: none;
  transition: $cubic;
  &:after {
    display: none;
  }
  span {
    font-size: 18px;
    line-height: 24px;
    color: $secondary;
  }
  svg {
    width: 28px;
    min-width: 28px;
    height: 18px;
    transition: $cubic;
  }
}
.btn-slider-arrow.swiper-button-prev {
  svg {
    order: -1;
    margin-right: 20px;
  }
}
.btn-slider-arrow.swiper-button-next {
  svg {
    order: 1;
    margin-left: 20px;
  }
}
header {
  background-color: $white;
}
.section-main {
  background-image: url('../img/services-page/services-section-main-bg.jpg');
  background-position-y: 116px;
  background-size: 100% calc(100% - 116px);
  h1 {
    font-size: 56px;
    line-height: 76px;
    max-width: 890px;
  }
  &-middle {
    flex-wrap: wrap;
    padding-top: 30px;
  }
  &-content {
    width: 60%;
    padding-top: 7%;
  }
  .breadcrumb {
    width: 100%;
    ul {
      display: flex;
      li + li {
        margin-left: 37px;
        &::before {
          content: '';
          position: absolute;
          top: 50%;
          left: -24px;
          display: block;
          width: 9px;
          height: 9px;
          background-image: url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNyIgaGVpZ2h0PSI4IiB2aWV3Qm94PSIwIDAgNyA4IiBmaWxsPSJub25lIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPgo8cGF0aCBkPSJNNyA0TDAuMjUgNy44OTcxMUwwLjI1IDAuMTAyODg1TDcgNFoiIGZpbGw9IiM3QTdEODAiLz4KPC9zdmc+Cg==');
          background-repeat: no-repeat;
          background-position: center;
          -webkit-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
          transform: translateY(-50%);
        }
      }
      li {
        position: relative;
        a, span {
          font-size: 14px;
          line-height: 32px;
          color: $secondary;
        }
        a {
          text-decoration: none;
        }
        span {
          opacity: .6;
        }
      }
    }
  }
  .services-main-content {
    &-advantages {
      margin-top: 62px;
      margin-bottom: 62px;
      ul {
