          display: flex; 
          align-items: center;
          div {
            min-width: 68px;
            width: 68px;
            margin-right: 32px;
          }
          p {
            font-size: 22px;
            line-height: 38px;
            color: $secondary;
          }
        }
      }
    }
  }
}
.cases-section {
  padding-top: 160px;
  h2 {
    margin-bottom: 42px;
  }
  h3 {
    margin-bottom: 62px;
  }
  .cases-arrows {
    margin-bottom: 62px;
    &-block {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  .cases-slider {
    &-item {
      &-content {
        margin-bottom: 118px;
      }
      &-img {
