}
.clients-logo {
  padding-top: 80px;
  padding-bottom: 160px;
  &-slider {
    .swiper-wrapper {
      -webkit-transition-timing-function: linear !important;
              transition-timing-function: linear !important;
    }
  }
  &-wrap {
    margin: auto;
    width: 100%;
    max-width: 226px;
    background-color: $white;
    img {
      mix-blend-mode: luminosity;
