}
.pricing {
  background-color: $tertiary;
  padding-top: 160px;
  padding-bottom: 160px;
  h2 {
    margin-bottom: 64px;
  }
  &-list {
    display: flex;
    justify-content: space-between;
    gap: 64px
  }
  &-item {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: $white;
    width: 100%;
    max-width: 517px;
    padding: 3.8%;
    border-top: 12px solid $secondary;
    &::before {
      position: absolute;
      content: '';
      top: -12px;
      left: 0;
      width: 0;
      height: 12px;
      background-color: $primary;
      z-index: 1;
      transition: $cubic;
    }
    &-cost {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      font-weight: 600;
      font-size: 36px;
      line-height: 44px;
      color: $secondary;
      margin-bottom: 42px;
      span:first-child {
        white-space: nowrap;
        margin-right: 25px;
      }
    }
    &-name {
      font-size: 28px;
      line-height: 36px;
      color: $secondary;
      margin-bottom: 24px;
    }
    &-desc {
      font-size: 22px;
      line-height: 38px;
      color: $secondary-light;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      margin-bottom: 42px;
    }
    &-list {
      display: flex;
      flex-direction: column;
      margin-bottom: 42px;
      gap: 32px;
      li {
        position: relative;
        font-size: 22px;
        line-height: 38px;
        padding-left: 70px;
        &::before {
          position: absolute;
          content: '';
          top: 0;
          left: 0;
          width: 39px;
          height: 39px;
          background-image: url("data:image/svg+xml;base64,");
          background-repeat: no-repeat;
          background-position: center center;
          background-size: contain
        }
      }
    }
    &-btn {
      margin-top: auto;
      a {
        width: 100%;
      }
    }
  }
  &-item.hit {
    .pricing-item-cost {
      color: $primary;
    }
  }
}
.search-tags {
  padding-top: 130px;
  padding-bottom: 80px;
  h2 {
    white-space: nowrap;
  }
  &-block {
