      margin-bottom: 18px;
    }
    &-desc {
      font-size: 22px;
      line-height: 42px;
      color: $secondary;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      overflow: hidden;
      margin-bottom: 32px;
    }
    &-name {
      font-size: 28px;
      line-height: 36px;
      color: $secondary;
      margin-bottom: 24px;
      margin-top: auto;
    }
    &__letter {
      font-size: 22px;
      line-height: 27px;
      color: $primary;
      text-decoration: none;
    }
  }
  &-more {
    display: flex;
    justify-content: center;
    width: 100%;
  }
}
.slogan {
  position: relative;
  background: linear-gradient(to right, $secondary 50%, $tertiary 50%);
  padding-top: 160px;
  padding-bottom: 160px;
  &-block {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &-text.light {
    p {
      color: $white;
    }
  }
  &-text.dark {
    p {
      color: $secondary;
    }
  }
  &-text {
    width: calc(50% - 60px);
    max-width: 663px;
    p {
      font-weight: 600;
      font-size: 36px;
      line-height: 52px;
      span {
        color: $primary
      }
    }
  }
  &__link {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 68px;
    height: 68px;
    min-width: 68px;
  }
}
.questions {
  padding-top: 160px;
  padding-bottom: 80px;
  h2 {
    text-align: center;
    margin-bottom: 64px;
  }
  &-accordion {
    display: flex;
    flex-direction: column;
    gap: 42px;
    width: 100%;
    max-width: 1386px;
    margin: auto;
    margin-bottom: 64px;
    &-item.active {
      background-color: transparent;
      .questions-accordion-item-question {
        p {
          &:after {
            width: 0;
          }
        }
        span {
          &::after {
            height: 0;
          }
        }
      }
    }
    &-item {
      padding: 41px 64px;
      background-color: $tertiary;
      transition: $cubic;
      &-question {
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        p {
          position: relative;
          font-size: 28px;
          line-height: 36px;
          color: $secondary;
          padding-bottom: 14px;
          &:after {
            position: absolute;
            content: '';
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: $secondary;
            transition: $cubic;
          }
        }
        span {
          position: relative;
          width: 68px;
          min-width: 68px;
          height: 68px;
          border: 1px solid $secondary;
          border-radius: 50%;
          &::after, &::before {
            position: absolute;
            content: '';
            top: 50%;
            left: 50%;
            background-color: $secondary;
            transition: $cubic;
            transform: translate(-50%, -50%);
          }
          &::before {
            width: 28px;
            height: 1px;
          }
          &::after {
            width: 1px;
            height: 28px;
          }
        }
      }
      &-answer {
        max-height: 0;
        overflow: hidden;
        transition: $cubic;
        &-wrap {
          max-width: 89%;
          padding-top: 41px;
          p {
            font-size: 22px;
            line-height: 42px;
            color: $secondary;
          }
        }
      }
    }
  }
  &-btn {
    display: flex;
    justify-content: center;
    width: 100%;
  }
}
.work-plan {
  padding-top: 160px;
  padding-bottom: 131px;
  background-color: $tertiary;
  h2 {
    margin-bottom: 84px;
  }
  &-steps {
    display: flex;
    justify-content: space-between;
    gap: 44px;
    &-item:last-child {
      .work-plan-steps-item__arrow {
        display: none;
      }
    }
    &-item.active {
      .work-plan-steps-item__label {
        background-color: $primary;
      }
      .work-plan-steps-item__arrow {
        max-width: 258px;
      }      
    }
    &-item {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      width: 25%;
      gap: 44px;
      &__label {
        transition: $cubic;
      }
      &-text {
        width: 100%;
        font-size: 28px;
        line-height: 46px;
        color: $secondary;
        padding-right: 35px;
      }
      &__arrow {
        max-width: 0;
        flex-grow: 1;
        height: 18px;
        background-image: url("data:image/svg+xml;base64,");
        background-position: center right;
        background-repeat: no-repeat;
        transition: $cubic;
        transition: all .6s;
      }
    }
  }
}
.text-section {
  padding-bottom: 160px;
  &-block {
    width: 100%;
    max-width: 1390px;
    margin: auto;
  }
  p {
    font-size: 28px;
    line-height: 56px;
    color: $secondary;
  }
}
.expert-question {
