          height: 100%;
        }
      }
      &__name {
        font-weight: 600;
        font-size: 22px;
        line-height: 24px;
        color: $secondary;
      }
    }
    &-desc {
      padding: 43px 64px;
      width: calc(100% - 82px);
      p {
        font-size: 22px;
        line-height: 42px;
        color: $secondary
      }
    }
  }
  &-more {
    display: flex;
    justify-content: center;
    width: 100%;
  }
}

